@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ================== Global variables ================== */
:root {
  --header-height-full: 5rem;
  --header-height: 3rem;

  /* Colors */
  --title-color: #212529;
  --title-color-effect: rgba(108, 117, 125, .7);
  --text-color: #495057;
  --background-color-light: #fff;
  --background-color-medium: hsl(0, 0%, 98%);
  --background-color-dark: rgba(233, 236, 239);

  /* Font and font size */
  --body-font: "Poppins", sans-serif;
  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --h4-font-size: 1.1rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /* Font weight */
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 550;
  --font-bold: 600;

  /* z index */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-project: 1000;
}

/* Large */
@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --h4-font-size: 0.975rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/* Medium */
@media screen and (max-width: 768px) {
  :root {
    --header-height-full: 0;
    
    --big-font-size: 2rem;
    --h1-font-size: 1.25rem;
    --h2-font-size: 1rem;
    --h3-font-size: 0.938rem;
    --h4-font-size: 0.9rem;
    --normal-font-size: 0.85rem;
    --small-font-size: 0.75rem;
    --smaller-font-size: 0.7rem;
  }
}

/* Small */
@media screen and (max-width: 350px) {
  :root {
    --big-font-size: 1.5rem;
    --h1-font-size: 1rem;
    --h2-font-size: 0.938rem;
    --h3-font-size: 0.85rem;
    --h4-font-size: 0.75rem;
    --small-font-size: 0.7rem;
    --smaller-font-size: 0.65rem;
  }
}

/* ================== Base ================== */
* {
  margin: 0;
  padding: 0;
  /* How the width and height of elements are calculated */
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  /* margin-top: var(--header-height-full); */
  color: var(--text-color);
  background-color: var(--background-color-medium);
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  /* Never exceeds the width of its container */
  max-width: 100%;
  /* Image maintains its aspect ratio */
  height: auto;
}

/* ================== Classes ================== */
/* Responsive layouts that adapt to different screen sizes */
.container {
  max-width: 70%;
  margin-left: auto;
  margin-right: auto;
}

/* A portion that groups related content together */
.section {
  padding: 6rem 0 6rem;
}

.section__title {
  font-size: var(--h1-font-size);
  color: var(--title-color);
  text-align: center;
  margin-bottom: 2rem;
}

.section__subtitle {
  display: block;
  font-size: var(--h3-font-size);
  text-align: center;
}

.box {
  background-color: var(--background-color-light);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.75rem;
  padding: 3rem 2rem;
}


/* ================== Buttons ================== */
.button {
  display: inline-block;
  background-color: var(--title-color);
  color: var(--background-color-medium);
  padding: .75rem .75rem;
  border-radius: .5rem;
  font-size: var(--h4-font-size);
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: rgba(108, 117, 125, 1);
}

.button__icon {
  font-size: var(--h3-font-size);
  margin-left: 0.25rem;
}

.button--flex {
  display: inline-flex;
  align-items: center;
  grid-column-gap: .5rem;
  column-gap: .5rem;
}

/* ================== Breakpoints ================== */
/* LLarge */
@media screen and (max-width: 1440px) {
  .container {
    max-width: 80%;
  }
}

/* Large */
@media screen and (max-width: 992px) {
  .button__icon {
    width: 22px;
    height: 22px;
  }
}

/* Medium */
@media screen and (max-width: 768px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }

  .section {
    padding: 2rem 0 2rem;
  }

  .button {
    font-size: .85rem;
  }
}

/* Small */
@media screen and (max-width: 350px) {
  .container {
    max-width: 92%;
  }
}
.header {
    width: 100%;
    /* Remains in the same position even when the page is scrolled */
    position: fixed;
    top: 0;
    z-index: var(--z-fixed);
    background-color: var(--background-color-medium);
}

.nav {
    height: var(--header-height-full);
    display: flex;
    /* Align flex items along the main axis of container */
    justify-content: flex-end;
    /* Align flex items along the cross axis of container*/
    align-items: center;
}

.nav__list {
    display: flex;
    grid-column-gap: 2.25rem;
    column-gap: 2.25rem;
}

.nav__link {
    display: flex;
    grid-column-gap: .5rem;
    column-gap: .5rem;
    flex-direction: column;
    align-items: center;
    color: var(--title-color);
    transition: .3s;
}

.nav__link:hover {
    color: var(--title-color-effect);
}

.nav__icon {
    font-size: var(--h3-font-size);
}

.nav__text {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.nav__close,
.nav__toggle {
    display: none;
}


/* ================== Breakpoints ================== */
/* Medium */
@media screen and (max-width: 768px) {
    .header {
        bottom: 0;
        top: auto;
    }

    .nav {
        height: var(--header-height);
    }

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--background-color-medium);
        padding: 1rem 1rem 4.5rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        transition: .9s;
    }

    .show-menu {
        bottom: 0;
        transition: .3s;
    }

    .nav__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 2rem;
        column-gap: 2rem;
        grid-row-gap: .5rem;
        row-gap: .5rem;
    }

    .home {
        grid-column: 2 / span 2;
    }

    .about,
    .projects {
        grid-column: 1 / span 2;
    }

    .skills,
    .contact {
        grid-column: 3 / span 2;
    }

    .nav__icon {
        font-size: var(--h1-font-size);
    }

    .nav__text {
        font-size: var(--h1-font-size);
        font-weight: var(--font-medium);
    }

    .nav__toggle,
    .nav__close {
        display: block;
        position: absolute;
        right: .75rem;
        bottom: .75rem;
        font-size: var(--h3-font-size);
        cursor: pointer;
        color: var(--title-color);
    }

    .hide-icon {
        display: none;
    }
}

/* Small */
@media screen and (max-width: 350px) {
    .nav__menu {
        padding: 1rem 0 3.5rem;
    }

    .nav__list {
        grid-column-gap: 3.5rem;
        column-gap: 3.5rem;
    }
}
.home__content {
    display: grid;
    grid-template-columns: 1fr 6fr 15fr;
    padding-top: var(--header-height-full);
    align-items: center;
}

/* ================== Social ================== */
.home__social {
    display: grid;
    /* Sets each column width = maximum content width */
    grid-template-columns: -webkit-max-content;
    grid-template-columns: max-content;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    order: 1;
    padding-right: var(--big-font-size);
    /* Align a grid item along the row axis within its grid cell */
    justify-self: end;
}

.home__social-icon {
    font-size: var(--h1-font-size);
    color: var(--title-color);
}

.home__social-icon:hover {
    color: var(--title-color-effect);
}

/* ================== Profile image ================== */
.home__img {
    order: 1;
    justify-self: center;
    width: 250px;
    height: auto;
    border-radius: 1.5rem;    
  }

/* ================== Description ================== */
.home__description {
    order: 3;
    justify-self: start;
    padding-left: var(--big-font-size);
}

.home__text {
    font-size: var(--h4-font-size);
    color: var(--text-color);
}

.home__title {
    font-size: var(--big-font-size);
    margin-bottom: 0.25rem;
}

.home__subtitle {
    position: relative;
    padding-left: 4.5rem;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: 1.5rem;
}

.home__subtitle::before {
    content: "";
    /* Positioned relative to its nearest positioned ancestor */
    position: absolute;
    width: 4rem;
    height: 1.25px;
    background-color: var(--title-color);
    left: 0;
    top: 1rem;
}

.home__buttons {
    display: flex;
    grid-column-gap: 2rem;
    column-gap: 2rem;
}

.home__buttons a:hover i {
    transform: translate(.15rem, -.15rem) scale(1.2);
}


/* ================== Breakpoints ================== */
/* Large */
@media screen and (max-width: 992px) {
    .home__img {
        width: 200px;
        height: auto;
    }

    .home__subtitle {
        padding-left: 3.5rem;
        margin-bottom: 1rem;
    }

    .home__subtitle::before {
        width: 3rem;
        top: 0.8rem;
    }
}

/* Medium */
@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 1fr 3fr;
        padding-top: var(--h2-font-size);
    }

    .home__social {
        grid-row-gap: .5rem;
        row-gap: .5rem;
        padding-right: 0;
        justify-self: center;
    }

    .home__social-icon {
        font-size: var(--big-font-size);
    }

    .home__description {
        justify-self: center;
        padding: 2rem 0 0;
        grid-column: 1 / span 2;
    }
}

/* Small */
@media screen and (max-width: 350px) {
    .home__img {
        width: 150px;
        height: auto;
    }

    .home__description {
        padding-left: 1rem;
    }
}
.about {
    scroll-margin-top: var(--header-height-full);
}

.about__container {
    display: grid;
    grid-template-columns: 4fr 7fr;
    align-items: center;
    grid-column-gap: 4rem;
    column-gap: 4rem;
}

.about__CV {
    display: flex;
    justify-content: center;
    margin: 1rem 0 5rem;
}

.about__title {
    display: flex;
    justify-content: center;
    grid-column-gap: .5rem;
    column-gap: .5rem;
    font-size: var(--h2-font-size);
    color: var(--title-color);
    font-weight: var(--font-semi-bold);
    margin-bottom: 1rem;
}

.about__CV a:hover i {
    transform: translate(0, .25rem) scale(1.1);
}

/* ================== Publification ================== */
.about__publification {
    align-self: start;
}

.pub__item {
    margin-bottom: 2rem;
}

.pub__link:hover {
    text-decoration: underline;
    color: var(--title-color);
}

.pubItem__title {
    color: var(--title-color);
    font-size: var(--h3-font-size);
    font-weight: var(--font-semi-bold);
}

.pubItem__author {
    margin-top: .5rem;
    font-size: var(--normal-font-size);
    color: var(--text-color);
}

.pubItem__publisher {
    margin-top: .25rem;
    font-size: var(--smaller-font-size);
    color: var(--text-color);
}


/* ================== Qualification ================== */
.about__qualification {
    align-self: start;
    padding-top: 3rem;
}

.qual__tabs {
    display: flex;
    justify-content: center;
    grid-column-gap: 28%;
    column-gap: 28%;
    align-items: flex-start;
}

.qual__container {
    display: grid;
    grid-template-columns: 0.95fr;
    justify-content: center;
}

.qual__data {
    display: grid;
    grid-template-columns: 0.8fr -webkit-max-content 1fr;
    grid-template-columns: 0.8fr max-content 1fr;
    grid-column-gap: 1.5rem;
    column-gap: 1.5rem;
    color: var(--title-color);
    padding-bottom: .5rem;
}

.qual__data__title {
    font-size: var(--h4-font-size);
    font-weight: var(--font-semi-bold);
}

.qual__data__ins,
.qual__data__date {
    font-size: var(--small-font-size);
    color: var(--text-color);
    display: flex;
    grid-column-gap: .35rem;
    column-gap: .35rem;
}

.qual_timeline {
    display: flex;
    align-items: center;
}

.qual__rounder {
    width: 12px;
    height: 12px;
    background-color: var(--text-color);
    border-radius: 50%;
}

.qual__line {
    width: 1px;
    height: 120%;
    background-color: var(--text-color);
    transform: translate(-6px, 0px);
}

/* ================== Breakpoints ================== */
/* Large */
@media screen and (max-width: 992px) {
    .about__container {
        grid-template-columns: 1fr;
    }
}
.skills {
    scroll-margin-top: var(--header-height-full);
}

.skills__container {
    display: grid;
    grid-template-columns: 40% 40%;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 4rem;
    row-gap: 4rem;
    grid-column-gap: 5%;
    column-gap: 5%;
    justify-content: center;
}

.skill__box {
    display: flex;
    flex-direction: column;
}

.skill__title {
    font-size: var(--h2-font-size);
    color: var(--title-color);
    font-weight: var(--font-semi-bold);
    margin-bottom: 2rem;
    text-align: center;
}

.skill__content {
    display: grid;
    grid-template-columns: repeat(3, -webkit-max-content);
    grid-template-columns: repeat(3, max-content);
    justify-content: space-between;
    grid-row-gap: .25rem;
    row-gap: .25rem;
}

.skill__data {
    display: flex;
    grid-column-gap: .5rem;
    column-gap: .5rem;
    color: var(--title-color);
    font-size: var(--h4-font-size);
    font-weight: var(--font-semi-bold);
}

/* ================== Breakpoints ================== */
/* LLarge */
@media screen and (max-width: 1440px) {
    .skill__content{
        grid-template-columns: repeat(2, -webkit-max-content);
        grid-template-columns: repeat(2, max-content);
        justify-content: space-around;
    }
}

/* Large */
@media screen and (max-width: 992px) {
    .skill__box {
        padding: 3rem 1rem;
    }

    .skills__container {
        grid-column-gap: 2rem;
        column-gap: 2rem;
    }

    .skill__title {
        margin-bottom: 1rem;
    }

    .skill__content{
        justify-content: space-between;
    }
}

/* Median */
@media screen and (max-width: 768px) {
    .skills__container {
        grid-template-columns: 1fr;
        grid-row-gap: 1rem;
        row-gap: 1rem;
    }

    .skill__box {
        padding: 1rem 0 2rem;
    }

    .skill__content{
        grid-template-columns: repeat(2, -webkit-max-content);
        grid-template-columns: repeat(2, max-content);
        justify-content: space-around;
    }
}
.lock-scroll {
    overflow: hidden;
}

.projects {
    scroll-margin-top: var(--header-height-full);
}

.projects__container {
    display: grid;
    grid-template-columns: repeat(2, 40%);
    grid-column-gap: 15%;
    column-gap: 15%;
    padding: 0 5%;
    justify-content: center;
}


/* ================== Item ================== */
.projectItem {
    display: grid;
    grid-template-columns: 100%;
    position: relative;
}

.projectItem__title {
    display: flex;
    justify-content: center;
    place-items: center;
    font-size: var(--h3-font-size);
    grid-column-gap: .75rem;
    column-gap: .75rem;
    text-align: center;
}

.projectItem__link {
    color: var(--title-color);
    font-size: calc(var(--h2-font-size) + 0.5rem);
}

.projectItem__link:hover {
    color: var(--title-color-effect)
}

.projectItem__intro {
    padding: 2rem .5rem 5rem;
    text-align: center;
    font-size: var(--h4-font-size);
    color: var(--text-color);
}

.projectItem__more {
    position: absolute;
    left: 2rem;
    bottom: 1.5rem;
    display: flex;
    align-items: center;
    width: -webkit-max-content;
    width: max-content;
    grid-column-gap: .25rem;
    column-gap: .25rem;
    cursor: pointer;
}

.projectItem__more:hover {
    color: var(--title-color-effect)
}

.projectItem__more:hover .projectItem__icon {
    transform: translate(.25rem) scale(1.2);
}

.projectItem__icon {
    font-size: var(--h3-font-size);
}

.projectItem__data {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: var(--z-project);
    visibility: hidden;
}

.projectItem__data-active {
    visibility: visible;
    overflow: hidden;
}

.projectItem__box {
    position: relative;
    width: 45%;
    height: 55%;
    overflow: auto;
}

.projectItem__detail {
    margin: 2rem;
    font-size: var(--h4-font-size);
    color: var(--title-color);
}

.projects__close {
    position: absolute;
    cursor: pointer;
    top: .75rem;
    right: 1rem;
    font-size: 1.5rem;
}

.projects__close:hover {
    color: var(--title-color-effect)
}

.projectItem__detail__title {
    display: flex;
    justify-content: center;
    font-size: var(--h2-font-size);
    margin-bottom: 2rem;
}

.projectItem__pair {
    display: flex;
    flex-direction: column;
    grid-row-gap: .25rem;
    row-gap: .25rem;
    margin-bottom: 1rem;
}

.projectItem__key {
    font-weight: var(--font-semi-bold);
}

.projectItem__content {
    padding-left: 2rem;
}

.projectItem__content ul {
    list-style-type: disc;
}

.projectItem__content li {
    margin-bottom: .5rem;
}

/* ================== Breakpoints ================== */
/* LLarge */
@media screen and (max-width: 1440px) {
    .projects__container {
        grid-template-columns: 80%;
        padding: 0;
        justify-content: center;
        grid-row-gap: 2rem;
        row-gap: 2rem;
    }

    .projectItem__intro {
        padding: 1.5rem 0.5rem 3.5rem;
    }

    .projectItem__box {
        width: 55%;
        height: 65%;
    }
}

/* Large */
@media screen and (max-width: 992px) {
    .projectItem__intro {
        padding: 1rem 0 2rem;
    }

    .projectItem__box {
        width: 70%;
        height: 80%;
    }
}

/* Median */
@media screen and (max-width: 768px) {
    .projects__container {
        grid-template-columns: 100%;
        grid-row-gap: 1rem;
        row-gap: 1rem;
    }

    .projectItem__box {
        width: 85%;
        height: 80%;
    }

    .projectItem__detail {
        margin: 1rem;
    }
}

/* Small */
@media screen and (max-width: 350px) {

    .projectItem__box {
        width: 90%;
        height: 90%;
    }

    .projectItem__detail {
        margin: 0rem;
    }
}
.contact {
    scroll-margin-top: var(--header-height-full);
}

.contact__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15%;
    column-gap: 15%;
}

.contact__content {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
}

.contact__card {
    background-color: var(--background-color-light);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact__logo {
    font-size: var(--h1-font-size);
    color: var(--title-color);
}

.contact__method {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    color: var(--title-color);
    margin-bottom: .5rem;
}

.contact__detail {
    margin-bottom: 2rem;
}

.contact__link {
    color: var(--title-color);
    font-size: var(--h4-font-size);
    display: flex;
    align-items: center;
    grid-column-gap: .25rem;
    column-gap: .25rem;
}

.contact__link:hover {
    color: var(--title-color-effect);
}

.contact__link:hover .contact__icon {
    transform: translate(.25rem) scale(1.2);
}

.contact__icon {
    font-size: var(--h3-font-size);
}

.contact__form {
    display: grid;
    align-items: start;
    width: 100%;
    grid-template-columns: 1fr;
    height: 100%;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.contact__form textarea {
    resize: none;
}

.contact__box__pair {
    display: grid;
    grid-template-columns: 1fr;
}

.contact__box__tag {
    font-size: var(--normal-font-size);
}

.contact__box__input {
    background-color: var(--background-color-light);
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    padding: .75rem 1rem;
}

.contact__send {
    padding: 1.25rem 2.5rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: var(--h3-font-size);
    justify-self: center;
    margin-top: 1rem;
}

.contact__send:hover i {
    transform: translate(.25rem) scale(1.2);
}

.contact__send__success {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: var(--z-project);
    visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.show-message{
    visibility: visible;
}

.contact__send__box {
    position: absolute;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: var(--background-color-light);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    padding: 1.5rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact__send__message {
    font-size: var(--h3-font-size);
    text-align: center;
}

/* ================== Breakpoints ================== */
/* Medium */
@media screen and (max-width: 768px) {
    .contact__container {
        grid-template-columns: 1fr;
        grid-row-gap: 1.5rem;
        row-gap: 1.5rem;
    }
  }
.footer {
    background-color: var(--background-color-light);
    border-top: 2px outset rgba(0, 0, 0, 0.1);
}

.footer__container {
    padding: 3rem 0 5rem;
    display: grid;
    justify-items: center;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.footer__title {
    color: var(--title-color);
}

.footer__list {
    display: flex;
    grid-column-gap: 2rem;
    column-gap: 2rem;
}

.footer__link {
    color: var(--text-color);
    font-size: var(--h3-font-size);
}

.footer__link:hover {
    color: var(--title-color-effect);
}

.footer__social {
    display: flex;
    grid-column-gap: 2rem;
    column-gap: 2rem;
}

.footer__social-icon {
    color: var(--title-color);
    font-size: 2rem;
}

.footer__social-icon:hover {
    color: var(--title-color-effect);
}

/* ================== Breakpoints ================== */
/* Medium */
@media screen and (max-width: 768px) {
    .footer__container {
        padding: 1rem 0 2rem;
        grid-row-gap: 1rem;
        row-gap: 1rem;
    }
    .footer__social-icon {
        font-size: 1.5rem;
    }
  }
.scrollup {
    position: fixed;
    right: 3rem;
    bottom: 3rem;
    background-color: var(--title-color);
    border-radius: 0.45rem;
    height: 3rem;
    width: 3rem;
    z-index: var(--z-tooltip);
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
}

.scrollup:hover {
    background-color: var(--title-color-effect);
}

.show-scroll {
    visibility: visible;
}

.scrollup__icon {
    font-size: 2rem;
    color: var(--background-color-light);
}

/* ================== Breakpoints ================== */
/* Large */
@media screen and (max-width: 992px) {
    .scrollup {
        height: 2.25rem;
        width: 2.25rem;
    }

    .scrollup__icon {
        font-size: 1.75rem;
    }
}

/* Medium */
@media screen and (max-width: 768px) {
    .scrollup {
        bottom: 3rem;
        right: 1rem;
    }
}

/* Medium */
@media screen and (max-width: 350px) {
    .scrollup {
        height: 1.5rem;
        width: 1.5rem;
    }

    .scrollup__icon {
        font-size: 1rem;
    }
}
